export default{
  data() {
    return {
      status: false
    }
  },
  onLoad() {
    let isMajor= uni.getStorageSync('isMajor'),
      token= uni.getStorageSync('token');
      
    if(isMajor && token) {
      uni.switchTab({
        url: './index'
      })
      return
    }
    this.status= true
    
  },
  methods: {
    goIndex(status) {
      uni.setStorage({
        key: 'isMajor',
        data: status
      })
      uni.switchTab({
        url: './index'
      })
    }
  }
}
